:host .filter-category-item {
  width: 100%;
  display: flex;
  align-items: center;
  border-top: solid;
  border-width: var(--mdx-comp-filter-flyout-elements-category-row-border-width-default);
  border-color: var(--mdx-comp-filter-flyout-elements-category-row-border-color-default);
  height: var(--mdx-comp-filter-flyout-elements-category-row-sizing-height);
  padding: 0;
  cursor: pointer;

  &__link {
    position: relative;
    width: 100%;
    display: flex;
    align-items: center;
    align-content: center;
    font: var(--mdx-comp-filter-flyout-elements-category-row-font-title);
    padding-top: var(--mdx-comp-filter-flyout-elements-category-row-spacing-vertical-padding);
    padding-bottom: var(--mdx-comp-filter-flyout-elements-category-row-spacing-vertical-padding);
    gap: var(--mdx-comp-filter-flyout-elements-category-row-spacing-gap-text-badge);

    &:hover {
      color: var(--mdx-comp-filter-flyout-elements-category-row-color-hover);
    }

    &-icon {
      position: absolute;
      right: 0;
      color: var(--mdx-comp-filter-flyout-elements-category-row-icon-color-default);
    }
  }
}

:host .filter-category-item__link:hover .filter-category-item__link-icon {
  color: var(--mdx-comp-filter-flyout-elements-category-row-color-hover);
}
