import { Component, Event, EventEmitter, Prop, h } from '@stencil/core';
import { BADGE_SIZES, BADGE_VARIANTS } from '../../../badge/exports';
import { ICON_SIZES } from '../../../icons/exports';

@Component({
  tag: 'mdx-filter-category-item',
  styleUrl: 'filter-category-item.scss',
  shadow: true,
})
export class FilterCategory {
  @Prop()
  ariaLabel: string;

  @Prop()
  categoryId: string;

  @Prop()
  name: string;

  @Prop()
  badge?: string;

  @Event()
  categoryClick: EventEmitter<{ categoryId: string }>;

  render() {
    return (
      <div class="filter-category-item">
        <a
          class="filter-category-item__link"
          aria-label={this.ariaLabel}
          onClick={() => this.categoryClick.emit({ categoryId: this.categoryId })}
        >
          <span class="filter-category-item__name">{this.name}</span>
          {this.badge && (
            <mdx-badge size={BADGE_SIZES.MD} variant={BADGE_VARIANTS.SECONDARY}>
              {this.badge}
            </mdx-badge>
          )}
          <mdx-icon-chevron-right size={ICON_SIZES.SM} class="filter-category-item__link-icon" />
        </a>
      </div>
    );
  }
}
